/*=============================
	Services One CSS
===============================*/
.service-one {
  position: relative;
  display: block;
  padding: 70px 0 120px;
  background-color: var(--thm-body-background);
  z-index: 1;
}

.service-one__top {
  position: relative;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  padding-bottom: 58px;
}

.service-one__top .sec-title {
  padding-bottom: 0px;
}

.service-one__top .btn-box {
  position: relative;
  display: block;
  top: -12px;
}

.service-one .row {
  --bs-gutter-x: 50px;
}

.service-one__single {
  position: relative;
  display: block;
  margin-bottom: 40px;
  padding-right: 20px;
  z-index: 1;
}

.service-one__single::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 150px;
  border: 1px solid var(--thm-primary);
  border-radius: 4px;
  transition: all 200ms linear;
  transition-delay: 0.1s;
}

.service-one__single:hover::before {
  border: 1px solid var(--thm-secondary);
}

.service-one__single:after {
  content: "";
  position: absolute;
  top: 20px;
  left: 21px;
  bottom: -20px;
  right: 0px;
  border-radius: 4px;
  background: rgba(var(--thm-secondary-rgb), 0.06);
  transition: all 200ms linear;
  transition-delay: 0.1s;
  z-index: -1;
}

.service-one__single:hover:after {
  background: #004b83;
}

.service-one__single-img {
  position: relative;
  display: block;
}

.service-one__single-img .inner {
  position: relative;
  display: block;
  overflow: hidden;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}

.service-one__single-img .inner::before {
  background: rgba(255, 255, 255, 0.3);
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 0;
  opacity: 1;
  z-index: 9;
  pointer-events: none;
}

.service-one__single:hover .service-one__single-img .inner::before {
  height: 100%;
  opacity: 0;
  -webkit-transition: all 400ms linear;
  transition: all 400ms linear;
}

.service-one__single-img .inner img {
  width: 100%;
  transition: all 500ms ease;
  transform: scale(1);
}

.service-one__single-img .inner .overlay-icon {
  position: absolute;
  bottom: 20px;
  right: 20px;
  z-index: 3;
}

.service-one__single-img .inner .overlay-icon img {
  width: 100%;
}

.service-one__single-content {
  position: relative;
  display: block;
  background-color: #ffffff;
  padding: 19px 20px 40px;
  border-bottom-right-radius: 4px;
  margin-left: 20px;
}

.service-one__single-content h3 {
  font-size: 24px;
  line-height: 34px;
  font-weight: 600;
  text-transform: capitalize;
  margin-bottom: 13px;
}

.service-one__single-content h3 a {
  color: var(--thm-heading-font-color);
  transition: all 200ms linear;
  transition-delay: 0.1s;
}

.service-one__single-content h3 a:hover {
  color: var(--thm-primary);
}

.service-one__single-content p {
  font-weight: 500;
  margin: 0;
}

.service-one__single-content .btn-box {
  position: relative;
  display: inline-block;
  line-height: 0;
  margin-top: 31px;
}

.service-one__single-content .btn-box a {
  position: relative;
  display: inline-block;
  color: var(--thm-secondary);
  font-size: 15px;
  line-height: 40px;
  font-family: var(--thm-body-font);
  font-weight: 600;
  border: 1px solid rgb(24, 98, 101, 0.2);
  border-radius: 4px;
  padding: 0px 20px 0px;
  transition: all 200ms linear;
  transition-delay: 0.1s;
}

.service-one__single-content .btn-box a span {
  position: relative;
  display: inline-block;
  font-size: 15px;
  margin-left: 4px;
  top: 2px;
  transition: all 200ms linear;
  transition-delay: 0.1s;
}

.service-one__single-content .btn-box a:hover {
  border-color: var(--thm-secondary);
  color: var(--thm-primary);
  background-color: var(--thm-secondary);
}

.service-one__single .service-one__single-content .btn-box a:hover span {
  color: var(--thm-primary);
}

.service-one .swiper-container {
  padding-left: 1px;
}

#service-one__pagination {
  position: relative;
  padding-top: 25px;
  z-index: 55;
}

#service-one__pagination .swiper-pagination-bullet {
  position: relative;
  width: 8px;
  height: 8px;
  margin: 0px 10px;
  background: #d0dadc;
  border-radius: 50%;
  opacity: 1;
  transition: all 200ms linear;
  transition-delay: 0.1s;
}

#service-one__pagination
  .swiper-pagination-bullet.swiper-pagination-bullet-active {
  background: var(--thm-secondary);
}

#service-one__pagination .swiper-pagination-bullet::before {
  position: absolute;
  top: -6px;
  left: -6px;
  bottom: -6px;
  right: -6px;
  border: 1px solid var(--thm-secondary);
  border-radius: 50%;
  transform: scaleX(0);
  transition: all 200ms linear;
  transition-delay: 0.1s;
  content: "";
}

#service-one__pagination
  .swiper-pagination-bullet.swiper-pagination-bullet-active::before,
#service-one__pagination .swiper-pagination-bullet:hover {
  transform: scaleX(1);
}

/*=============================
	Services Two Css
===============================*/
.services-two {
  position: relative;
  display: block;
  padding: 120px 0px 100px;
  z-index: 1;
}

.services-two .container {
  position: relative;
}

.services-two__bg {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-attachment: scroll;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  z-index: -1;
}

.services-two .shape1 {
  position: absolute;
  top: 0;
  left: 0;
  mix-blend-mode: soft-light;
  opacity: 0.5;
  z-index: -1;
}

.services-two__bg::before {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(var(--thm-black-rgb), 0.8);
  content: "";
  z-index: -1;
}

.services-two .sec-title-two .sub-title {
  background: rgba(var(--thm-black-rgb), 0.6);
}

.services-two .sec-title-two h2 {
  color: var(--thm-white);
}

.services-two__single {
  position: relative;
  display: block;
  z-index: 1;
}

.services-two__single-img {
  position: relative;
  display: block;
  margin-left: 20px;
  margin-right: 20px;
  z-index: 2;
}

.services-two__single-img .number-box {
  position: absolute;
  right: 20px;
  bottom: -15px;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--thm-primary);
  border-radius: 50%;
  border: 2px solid var(--thm-white);
  overflow: hidden;
  color: var(--thm-white);
  font-size: 20px;
  font-weight: 700;
  line-height: 20px;
  transform: scale(0.7);
  opacity: 0;
  -webkit-transition: all 900ms ease;
  transition: all 900ms ease;
  z-index: 5;
}

.services-two__single:hover .services-two__single-img .number-box {
  transform: scale(1);
  opacity: 1;
}

.services-two__single-img .inner {
  position: relative;
  display: block;
  border-radius: 4px;
  overflow: hidden;
  z-index: 1;
}

.services-two__single-img .inner::before {
  background: rgba(255, 255, 255, 0.4);
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 0;
  opacity: 1;
  z-index: 1;
  pointer-events: none;
}

.services-two__single:hover .services-two__single-img .inner::before {
  height: 100%;
  opacity: 0;
  -webkit-transition: all 400ms linear;
  transition: all 400ms linear;
}

.services-two__single-img .inner img {
  width: 100%;
  transition: 0.5s ease;
  transform: scale(1.05);
}

.services-two__single:hover .services-two__single-img .inner img {
  transform: scale(1);
}

.services-two__single-content {
  position: relative;
  display: block;
  margin-top: -85px;
  overflow: hidden;
}

.services-two__single-content-inner {
  position: relative;
  display: block;
  padding: 107px 30px 35px;
  border-radius: 4px;
  overflow: hidden;
  z-index: 1;
}

.services-two__single-content-inner::before {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0px;
  right: 0;
  background: var(--thm-white);
  border-radius: 4px;
  content: "";
  z-index: -1;
}

.services-two__single-content-inner h3 {
  font-size: 20px;
  line-height: 30px;
  font-weight: 600;
  margin-bottom: 9px;
}

.services-two__single-content-inner h3 a {
  color: var(--thm-black);
}

.services-two__single-content-inner h3 a:hover {
  color: var(--thm-primary);
}

.services-two__single-content-inner p {
  font-weight: 500;
  margin: 0;
  margin-bottom: 6px;
  font-size: 15px;
}

.services-two__single-content-inner .count-box {
  position: absolute;
  left: 30px;
  bottom: -15px;
  color: rgba(1, 28, 57, 0.06);
  font-size: 40px;
  line-height: 50px;
  font-weight: 700;
  font-family: var(--thm-heading-font);
  opacity: 1;
  -webkit-transform: translateY(0px);
  transform: translateY(0px);
  -webkit-transition: all 900ms ease;
  transition: all 900ms ease;
}

.services-two__single:hover .services-two__single-content-inner .count-box {
  opacity: 0;
  -webkit-transform: translateY(30px);
  transform: translateY(30px);
}

.services-two__single-content .btn-box {
  position: relative;
  display: block;
  margin-top: -20px;
  margin-left: 30px;
  opacity: 0;
  -webkit-transform: translateY(30px);
  transform: translateY(30px);
  -webkit-transition: all 900ms ease;
  transition: all 900ms ease;
  z-index: 2;
}

.services-two__single:hover .services-two__single-content .btn-box {
  opacity: 1;
  -webkit-transform: translateY(0px);
  transform: translateY(0px);
}

.services-two__single-content .btn-box .thm-btn {
  padding-top: 1px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 0px;
  color: var(--thm-white);
}

.services-two__single-content .btn-box .thm-btn::before {
  background: var(--thm-black);
}

.services-two .swiper-nav-style1 {
  top: 45px;
}

.services-two__wrap {
  position: absolute;
  top: 55px;
  right: 190px;
  z-index: 100;
}

.services-two__wrap .swiper-counter {
  position: relative;
  display: inline-flex;
  align-items: baseline;
  justify-content: space-between;
  width: 40px;
  z-index: 9;
}

.services-two__wrap .swiper-counter div {
  position: relative;
  display: inline-block;
  color: var(--thm-white);
  font-size: 24px;
  line-height: 24px;
  font-weight: 500;
  font-family: var(--thm-heading-font);
}

.services-two__wrap .swiper-counter div#current2 {
  margin-right: 6px;
}

.services-two__wrap .swiper-counter div#total2 {
  position: relative;
  top: 15px;
  right: -5px;
  padding-left: 2px;
  color: rgba(255, 255, 255, 0.4);
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  font-family: var(--thm-heading-font);
}

.services-two__wrap .swiper-counter div#total2:before {
  content: "/";
  position: absolute;
  left: -10px;
  width: 0px;
  height: 0px;
  top: -5px;
}

